import React from 'react';
import ReactDOM from 'react-dom'
import { BrowserRouter, Route, Switch } from 'react-router-dom';
import { Home } from './components/Home.js';
import { Arduboy } from './components/Arduboy.js';



class App extends React.Component {
  render() {
    return (      
       <BrowserRouter>
        <div>
          
            <Switch>
             <Route path="/FreezingSnail.github.io/" component={Home} exact/>
             <Route path="https://freezesnail.github.io/FreezingSnail.github.io/Arduboy" component={Arduboy} exact/>

           </Switch>
        </div> 
      </BrowserRouter>
    );
  }
}

export default App;
